@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Encode Sans Expanded', sans-serif;
}

.cards {
	padding: 4rem;
	background: #fff;
}

h1 {
	text-align: center;
}

h7 {
	color: black;
}

.cards__container {
	display: flex;
	flex-flow: column;
	align-items: center;
	max-width: 1120px;
	width: 90%;
	margin: 0 auto;
}

.cards__wrapper {
	position: relative;
	margin: 50px 0 45px;
}

.cards__items {
	margin-bottom: 24px;
}

.cards__item {
	display: flex;
	flex: 1 1;
	margin: 0 1rem;
	border-radius: 10px;
}

.cards__item__link {
	display: flex;
	flex-flow: column;
	width: 100%;
	box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
	-webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
	filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
	border-radius: 10px;
	overflow: hidden;
	text-decoration: none;
}

.cards__item__pic-wrap {
	position: relative;
	width: 100%;
	padding-top: 67%;
	overflow: hidden;
}

.fade-img {
	animation-name: fade-img;
	animation-duration: 2s;
}

.cards__item__pic-wrap::after {
	content: attr(data-category);
	position: absolute;
	bottom: 0;
	margin-left: 10px;
	padding: 6px 8px;
	max-width: calc((100%) - 60px);
	font-size: 12px;
	font-weight: 400;
	color: black;
	background-color: #f0db4f;
	box-sizing: border-box;
}

.cards__item__img {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: block;
	width: 100%;
	max-width: 100%;
	height: 100%;
	max-height: 100%;
	object-fit: cover;
	transition: all 0.2s linear;
}

.cards__item__img:hover {
	transform: scale(1.1);
}

.cards__item__info {
	padding: 20px 30px 30px;
}

.cards__item__info a {
	color: #323330;
	text-decoration: none;
	height: 2px;
}

.cards__item__text {
	color: #99a0b3;
	font-size: 14px;
	line-height: 24px;
	margin-top: 10px;
	margin-bottom: 20px;
}

.iconLinks a {
	margin-top: 10px;
	width: 100%;
	display: inline-block;
}

.iconLinks p {
	width: 50%;
	display: inline-block;
	padding-left: 10px;
}

@media only screen and (min-width: 1200px) {
	.content__blog__container {
		width: 84%;
	}
}

@media only screen and (min-width: 1024px) {
	.cards__items {
		display: flex;
	}
}

@media only screen and (max-width: 1024px) {
	.cards__item {
		margin-bottom: 2rem;
	}
}

.main__container {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	padding: 60px 60px;
	background-color: #323330;
	height: 93vh;
}

.main__container h1 {
	font-size: 110px;
	color: #8a8a8a;
	-webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: #8a8a8a;
	margin-bottom: 32px;
	text-align: left;
}

.main__content {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 700px;
}

.main__text {
	padding-right: 20px;
}

.main__text h2 {
	font-size: 48px;
	line-height: 1.5;
	margin-bottom: 32px;
	color: #8a8a8a;
}

.main__text p {
	line-height: 2.3;
	color: #8a8a8a;
	margin-top: 30px;
}

.main__image {
	display: flex;
	justify-content: center;
}

.main__img {
	width: 420px;
	height: 570px;
}

/* Media Queries */
@media screen and (max-width: 1000px) {
	.main__section {
		padding: 30px;
	}
	.main__content {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr;
	}

	.main__image {
		margin-top: 40px;
	}

	.main__container h1 {
		font-size: 70px;
	}
}

@media screen and (max-width: 468px) {
	.main__text h2 {
		font-size: 32px;
	}

	.main__img {
		width: 100%;
		height: 100%;
	}
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body,
input,
textarea {
	font-family: 'Poppins', sans-serif;
}

.container {
	position: relative;
	width: 100%;
	min-height: 100vh;
	padding: 2rem;
	background-color: white;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
}

.form {
	width: 100%;
	max-width: 820px;
	border-radius: 10px;
	box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
	z-index: 1;
	overflow: hidden;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}

.contact-form {
	background-color: #f0db4f;
	position: relative;
}

.circle.one {
	width: 130px;
	height: 130px;
	top: 130px;
	right: -40px;
}

.circle.two {
	width: 80px;
	height: 80px;
	top: 10px;
	right: 30px;
}

.contact-form:before {
	content: '';
	position: absolute;
	width: 26px;
	height: 26px;
	background-color: white;
	transform: rotate(45deg);
	top: 50px;
	left: -13px;
}

form {
	padding: 2.3rem 2.2rem;
	z-index: 10;
	overflow: hidden;
	position: relative;
}

.title {
	color: #323330;
	font-weight: 500;
	font-size: 1.5rem;
	line-height: 1;
	margin-bottom: 0.7rem;
}

.input-container {
	position: relative;
	margin: 1rem 0;
}

.input {
	width: 100%;
	outline: none;
	border: 2px solid #323330;
	background: none;
	padding: 0.6rem 1.2rem;
	color: #323330;
	font-weight: 500;
	font-size: 0.95rem;
	letter-spacing: 0.5px;
	border-radius: 25px;
	transition: 0.3s;
}

textarea.input {
	padding: 0.8rem 1.2rem;
	min-height: 150px;
	border-radius: 22px;
	resize: none;
	overflow-y: auto;
}

::-webkit-input-placeholder {
	opacity: 1;
	position: absolute;
	top: 50%;
	left: 15px;
	padding: 0 0.4rem;
	color: rgb(36, 35, 35);
	font-size: 0.9rem;
	font-weight: 400;
}

:-ms-input-placeholder {
	opacity: 1;
	position: absolute;
	top: 50%;
	left: 15px;
	padding: 0 0.4rem;
	color: rgb(36, 35, 35);
	font-size: 0.9rem;
	font-weight: 400;
}

::placeholder {
	opacity: 1;
	position: absolute;
	top: 50%;
	left: 15px;
	padding: 0 0.4rem;
	color: rgb(36, 35, 35);
	font-size: 0.9rem;
	font-weight: 400;
}

.input-container.textarea label {
	top: 1rem;
	transform: translateY(0);
}

.btn {
	padding: 0.6rem 1.3rem;
	background-color: transparent;
	border: 2px solid black;
	font-size: 0.95rem;
	color: black;
	line-height: 1;
	border-radius: 25px;
	outline: none;
	cursor: pointer;
	transition: 0.3s;
	margin: 0;
}

.btn:hover {
	background-color: black;
	color: #fff;
}

.input-container span:before,
.input-container span:after {
	content: '';
	position: absolute;
	width: 10%;
	opacity: 0;
	transition: 0.3s;
	height: 5px;
	background-color: white;
	top: 50%;
	transform: translateY(-50%);
}

.input-container span:before {
	left: 50%;
}

.input-container span:after {
	right: 50%;
}

.input-container.focus label {
	top: 0;
	transform: translateY(-50%);
	left: 25px;
	font-size: 0.8rem;
}

.input-container.focus span:before,
.input-container.focus span:after {
	width: 50%;
	opacity: 1;
}

.contact-info {
	padding: 2.3rem 2.2rem;
	position: relative;
	background-color: white;
}

.contact-info .title {
	color: black;
}

.text {
	color: grey;
	margin: 1.5rem 0 2rem 0;
}

.information {
	display: flex;
	color: red;
	margin: 0.7rem 0;
	align-items: center;
	font-size: 0.95rem;
}

.information p {
	color: grey;
}

.icon {
	width: 28px;
	margin-right: 1.2rem;
	color: black;
}

.social-media {
	padding: 2rem 0 0 0;
}

.social-media p {
	color: rgb(146, 144, 144);
}

.social-icons {
	display: flex;
	margin-top: 0.5rem;
}

.social-icons-link {
	width: 35px;
	height: 35px;
	border-radius: 5px;
	background: linear-gradient(45deg, #10201d, black);
	color: #f0db4f;
	text-align: center;
	line-height: 35px;
	margin-right: 0.5rem;
	transition: 0.3s;
}

.social-icons a:hover {
	transform: scale(1.05);
}

.linkedin {
	color: white;
}

.github {
	color: white;
}

.facebook {
	color: white;
}

.canva {
	color: white;
}

.contact-info:before {
	content: '';
	position: absolute;
	width: 110px;
	height: 100px;
	border: 22px solid #f0db4f;
	border-radius: 50%;
	bottom: -77px;
	right: 50px;
	opacity: 0.3;
}

.square {
	position: absolute;
	height: 400px;
	top: 50%;
	left: 50%;
	transform: translate(181%, 11%);
	opacity: 0.2;
}

@media (max-width: 850px) {
	.form {
		grid-template-columns: 1fr;
	}

	.contact-info:before {
		bottom: auto;
		bottom: initial;
		top: -75px;
		right: 65px;
		transform: scale(0.95);
	}

	.contact-form:before {
		top: -13px;
		left: auto;
		left: initial;
		right: 70px;
	}

	.square {
		transform: translate(140%, 43%);
		height: 350px;
	}

	.big-circle {
		bottom: 75%;
		transform: scale(0.9) translate(-40%, 30%);
		right: 50%;
	}

	.text {
		margin: 1rem 0 1.5rem 0;
	}

	.social-media {
		padding: 1.5rem 0 0 0;
	}
}

@media (max-width: 480px) {
	.container {
		padding: 1.5rem;
	}

	.contact-info:before {
		display: none;
	}

	.square,
	.big-circle {
		display: none;
	}

	form,
	.contact-info {
		padding: 1.7rem 1.6rem;
	}

	.text,
	.information,
	.social-media p {
		font-size: 0.8rem;
	}

	.title {
		font-size: 1.15rem;
	}

	.social-icons a {
		width: 30px;
		height: 30px;
		line-height: 30px;
		color: white;
	}

	.icon {
		width: 23px;
	}

	.input {
		padding: 0.45rem 1.2rem;
	}

	.btn {
		padding: 0.45rem 1.2rem;
	}
}

.Timeline {
	background: #3da3d5;
	color: rgb(53, 53, 53);
}

.timelineTitle {
	font-size: 10em;
	text-align: center;
	font-family: 'Bebas Neue', sans-serif;
}

h3 {
	padding-top: 0.25em;
}

.vertical-timeline-element-content {
	box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
		0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
	padding: 2em 3em !important;
}

.date {
	color: rgb(201, 251, 255);
}

#description {
	margin: 1.5em 0 2em 0;
}

.button {
	text-decoration: none;
	padding: 0.5em 1em;
	border-radius: 5px;
	color: white;
}

.workButton {
	background-color: #06d6a0;
}

.workButton:hover {
	background-color: #0ac593;
}

.schoolButton {
	background-color: #f9c74f;
}

.schoolButton:hover {
	background-color: #f3bc3c;
}

@media only screen and (max-width: 1700px) {
	.vertical-timeline-element-date {
		display: block !important;
		float: none !important;
		color: rgb(44, 44, 44);
		margin-top: 1.5em;
	}
}

