@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body,
input,
textarea {
	font-family: 'Poppins', sans-serif;
}

.container {
	position: relative;
	width: 100%;
	min-height: 100vh;
	padding: 2rem;
	background-color: white;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
}

.form {
	width: 100%;
	max-width: 820px;
	border-radius: 10px;
	box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
	z-index: 1;
	overflow: hidden;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}

.contact-form {
	background-color: #f0db4f;
	position: relative;
}

.circle.one {
	width: 130px;
	height: 130px;
	top: 130px;
	right: -40px;
}

.circle.two {
	width: 80px;
	height: 80px;
	top: 10px;
	right: 30px;
}

.contact-form:before {
	content: '';
	position: absolute;
	width: 26px;
	height: 26px;
	background-color: white;
	transform: rotate(45deg);
	top: 50px;
	left: -13px;
}

form {
	padding: 2.3rem 2.2rem;
	z-index: 10;
	overflow: hidden;
	position: relative;
}

.title {
	color: #323330;
	font-weight: 500;
	font-size: 1.5rem;
	line-height: 1;
	margin-bottom: 0.7rem;
}

.input-container {
	position: relative;
	margin: 1rem 0;
}

.input {
	width: 100%;
	outline: none;
	border: 2px solid #323330;
	background: none;
	padding: 0.6rem 1.2rem;
	color: #323330;
	font-weight: 500;
	font-size: 0.95rem;
	letter-spacing: 0.5px;
	border-radius: 25px;
	transition: 0.3s;
}

textarea.input {
	padding: 0.8rem 1.2rem;
	min-height: 150px;
	border-radius: 22px;
	resize: none;
	overflow-y: auto;
}

::placeholder {
	opacity: 1;
	position: absolute;
	top: 50%;
	left: 15px;
	padding: 0 0.4rem;
	color: rgb(36, 35, 35);
	font-size: 0.9rem;
	font-weight: 400;
}

.input-container.textarea label {
	top: 1rem;
	transform: translateY(0);
}

.btn {
	padding: 0.6rem 1.3rem;
	background-color: transparent;
	border: 2px solid black;
	font-size: 0.95rem;
	color: black;
	line-height: 1;
	border-radius: 25px;
	outline: none;
	cursor: pointer;
	transition: 0.3s;
	margin: 0;
}

.btn:hover {
	background-color: black;
	color: #fff;
}

.input-container span:before,
.input-container span:after {
	content: '';
	position: absolute;
	width: 10%;
	opacity: 0;
	transition: 0.3s;
	height: 5px;
	background-color: white;
	top: 50%;
	transform: translateY(-50%);
}

.input-container span:before {
	left: 50%;
}

.input-container span:after {
	right: 50%;
}

.input-container.focus label {
	top: 0;
	transform: translateY(-50%);
	left: 25px;
	font-size: 0.8rem;
}

.input-container.focus span:before,
.input-container.focus span:after {
	width: 50%;
	opacity: 1;
}

.contact-info {
	padding: 2.3rem 2.2rem;
	position: relative;
	background-color: white;
}

.contact-info .title {
	color: black;
}

.text {
	color: grey;
	margin: 1.5rem 0 2rem 0;
}

.information {
	display: flex;
	color: red;
	margin: 0.7rem 0;
	align-items: center;
	font-size: 0.95rem;
}

.information p {
	color: grey;
}

.icon {
	width: 28px;
	margin-right: 1.2rem;
	color: black;
}

.social-media {
	padding: 2rem 0 0 0;
}

.social-media p {
	color: rgb(146, 144, 144);
}

.social-icons {
	display: flex;
	margin-top: 0.5rem;
}

.social-icons-link {
	width: 35px;
	height: 35px;
	border-radius: 5px;
	background: linear-gradient(45deg, #10201d, black);
	color: #f0db4f;
	text-align: center;
	line-height: 35px;
	margin-right: 0.5rem;
	transition: 0.3s;
}

.social-icons a:hover {
	transform: scale(1.05);
}

.linkedin {
	color: white;
}

.github {
	color: white;
}

.facebook {
	color: white;
}

.canva {
	color: white;
}

.contact-info:before {
	content: '';
	position: absolute;
	width: 110px;
	height: 100px;
	border: 22px solid #f0db4f;
	border-radius: 50%;
	bottom: -77px;
	right: 50px;
	opacity: 0.3;
}

.square {
	position: absolute;
	height: 400px;
	top: 50%;
	left: 50%;
	transform: translate(181%, 11%);
	opacity: 0.2;
}

@media (max-width: 850px) {
	.form {
		grid-template-columns: 1fr;
	}

	.contact-info:before {
		bottom: initial;
		top: -75px;
		right: 65px;
		transform: scale(0.95);
	}

	.contact-form:before {
		top: -13px;
		left: initial;
		right: 70px;
	}

	.square {
		transform: translate(140%, 43%);
		height: 350px;
	}

	.big-circle {
		bottom: 75%;
		transform: scale(0.9) translate(-40%, 30%);
		right: 50%;
	}

	.text {
		margin: 1rem 0 1.5rem 0;
	}

	.social-media {
		padding: 1.5rem 0 0 0;
	}
}

@media (max-width: 480px) {
	.container {
		padding: 1.5rem;
	}

	.contact-info:before {
		display: none;
	}

	.square,
	.big-circle {
		display: none;
	}

	form,
	.contact-info {
		padding: 1.7rem 1.6rem;
	}

	.text,
	.information,
	.social-media p {
		font-size: 0.8rem;
	}

	.title {
		font-size: 1.15rem;
	}

	.social-icons a {
		width: 30px;
		height: 30px;
		line-height: 30px;
		color: white;
	}

	.icon {
		width: 23px;
	}

	.input {
		padding: 0.45rem 1.2rem;
	}

	.btn {
		padding: 0.45rem 1.2rem;
	}
}
